import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import CryptoExchangeScreen from "../screens/crypto-exchange";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const CryptoDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Cryptocurrency Exchange Development Services", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <CryptoExchangeScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Cryptocurrency Exchange Development Services"
    description="Revolutionize digital finance with Justtry Technologies. We specialize in cryptocurrency exchange development services, delivering cutting-edge solutions for secure, scalable, and user-friendly trading platforms. Elevate your crypto venture today."
    keywords="Cryptocurrency Exchange Development, White Label Crypto Exchange, Custom Crypto Exchange Solutions, Centralized Exchange (CEX) Development, Crypto Trading Platform Development, Exchange Software Development, Crypto Exchange API Integration, Exchange Liquidity Solutions, Cryptocurrency Wallet Integration, Security Token Exchange, Margin Trading Platform Development, Peer-to-Peer (P2P) Exchange Development, Futures and Options Trading, Cryptocurrency Payment Gateway, Exchange Liquidity Aggregation, KYC and AML Compliance Solutions, Multi-Currency Support, Fiat-to-Crypto Exchange Development, Token Listing Services, Cryptocurrency Custody Solutions, Mobile App Development for Crypto Exchanges, Cryptocurrency Exchange Software Customization, Regulatory Compliance Services, OTC (Over-the-Counter) Trading Solutions"
  ></Seo>
);

export default CryptoDevelopment;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
